import trailerStars from "../../components/trailerStars.vue"

var _self;
export default {
	data() {
		return {
			text:["书评","系统"],
			TabCur: 0,
			scrollLeft: 0,
			id:1,
			lists:[],
		}
	},
	onLoad() {
		_self = this;
		this.refresh()
	},
	methods: {
		refresh() {
			uni.showLoading({
				mask: true,
				title: "请稍后..."
			});
			uni.request({
				url: this.serverUrl + "/comment/getCommentByUserId?userId=" + this.userId,
				method: 'GET',
				success: res => {
					var lists = res.data.data;
					for (let i = 0; i < lists.length; i++) {
						_self.requestGetBook(i,lists)
					}
				},
			})
		},
		requestGetBook(index,lists) {
			uni.request({
				url: this.serverUrl + "/book/getBookById?bookId=" + lists[index].bookId,
				method: 'GET',
				success(res) {
					lists[index].book = res.data.data;
					_self.lists.push(lists[index])
				},
				complete(res) {
					uni.hideNavigationBarLoading();
					uni.hideLoading();
					uni.stopPullDownRefresh();
				}
			})

		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
			}
		
	},
	components:{
		trailerStars
	}
}
